import React, { useCallback, useState, useEffect } from "react";
import { Outlet, NavLink, useLocation, useNavigate } from "react-router-dom";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import Divider from "@mui/material/Divider";
import IconButton from "@mui/material/IconButton";
import Tooltip from "@mui/material/Tooltip";
import PersonAdd from "@mui/icons-material/PersonAdd";
import Settings from "@mui/icons-material/Settings";
import Logout from "@mui/icons-material/Logout";
import CookieTwoToneIcon from "@mui/icons-material/CookieTwoTone";

import InputBase from "@mui/material/InputBase";
import SearchIcon from "@mui/icons-material/Search";
import GridViewIcon from "@mui/icons-material/GridView";

import SearchDialog from "./SearchDialog";

import Root from "./root";

import "./home.css";

function Home() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const [dlob, setDlog] = useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    let sinput = document.getElementById("search-input");
    if (!sinput) return;
    sinput.onfocus = () => {
      console.log(dlob);
      dlob.handleClickOpen();
    };
    // 清除副作用
    return () => {
      sinput = null;
    };
  }, [dlob]);

  const isLogin = sessionStorage.getItem("user");

  // 记忆化内容转跳
  const renderConvert = useCallback(() => {
    console.log(pathname);
    if (pathname === "/home") {
      return <Root />;
    } else {
      return <Outlet />;
    }
  }, [pathname]);

  // 比较
  //   const renderConvert = () => {
  //     console.log(pathname);
  //     if (pathname === "/home") {
  //       return <div>home</div>;
  //     } else {
  //       return <Outlet />;
  //     }
  //   };

  const logout = () => {
    setTimeout(() => {
      sessionStorage.getItem("user") && sessionStorage.removeItem("user");
      navigate("/", {
        replace: true,
      });
    }, 3000);
  };

  return (
    <div style={{ height: "100%" }}>
      <header className="header-wrap">
        <Box
          sx={{
            display: "flex",
            alignItems: "stretch",
            textAlign: "center",
            height: "100%",
          }}
        >
          <div
            style={{
              cursor: "pointer",
              display: "flex",
              alignItems: "center",
              marginRight: "40px",
            }}
          >
            <CookieTwoToneIcon fontSize="large" />
          </div>

          <NavLink
            to={"/home"}
            className={({ isActive }) => (isActive ? "nav-active" : undefined)}
            end
          >
            首页
          </NavLink>

          <NavLink
            to={"/home/frontend"}
            className={({ isActive }) => (isActive ? "nav-active" : undefined)}
            end
          >
            前端
          </NavLink>

          <NavLink
            to={"/home/program"}
            className={({ isActive }) => (isActive ? "nav-active" : undefined)}
            end
          >
            编程
          </NavLink>

          <NavLink
            to={"/home/photography"}
            className={({ isActive }) => (isActive ? "nav-active" : undefined)}
            end
          >
            摄影
          </NavLink>
        </Box>
        {isLogin ? (
          <div style={{ display: "flex" }}>
            <div className="search">
              <InputBase
                id="search-input"
                sx={{ ml: 1, flex: 1 }}
                placeholder="搜索..."
                inputProps={{ "aria-label": "search google maps" }}
              />
              <IconButton type="button" sx={{ p: "10px" }} aria-label="search">
                <SearchIcon />
              </IconButton>
            </div>
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                textAlign: "center",
              }}
            >
              <Tooltip title="Account settings">
                <IconButton
                  onClick={handleClick}
                  size="small"
                  sx={{ ml: 2 }}
                  aria-controls={open ? "account-menu" : undefined}
                  aria-haspopup="true"
                  aria-expanded={open ? "true" : undefined}
                >
                  <Avatar sx={{ width: 32, height: 32 }}>M</Avatar>
                </IconButton>
              </Tooltip>
            </Box>
            <Menu
              anchorEl={anchorEl}
              id="account-menu"
              open={open}
              onClose={handleClose}
              onClick={handleClose}
              PaperProps={{
                elevation: 0,
                sx: {
                  overflow: "visible",
                  filter: "drop-shadow(0px 2px 8px rgba(0,0,0,0.32))",
                  mt: 1.5,
                  "& .MuiAvatar-root": {
                    width: 32,
                    height: 32,
                    ml: -0.5,
                    mr: 1,
                  },
                  "&:before": {
                    content: '""',
                    display: "block",
                    position: "absolute",
                    top: 0,
                    right: 14,
                    width: 10,
                    height: 10,
                    bgcolor: "background.paper",
                    transform: "translateY(-50%) rotate(45deg)",
                    zIndex: 0,
                  },
                },
              }}
              transformOrigin={{ horizontal: "right", vertical: "top" }}
              anchorOrigin={{ horizontal: "right", vertical: "bottom" }}
            >
              <MenuItem>
                <Avatar />
                <NavLink
                  to={"/about"}
                  className={({ isActive }) =>
                    isActive ? "nav-active" : undefined
                  }
                >
                  关于我
                </NavLink>
              </MenuItem>
              <MenuItem>
                <Avatar /> My account
              </MenuItem>
              <Divider />
              <MenuItem>
                <ListItemIcon>
                  <GridViewIcon />
                </ListItemIcon>
                <NavLink to={"/admin/board"} replace={true}>
                  后台
                </NavLink>
              </MenuItem>
              <MenuItem>
                <ListItemIcon>
                  <Settings fontSize="small" />
                </ListItemIcon>
                Settings
              </MenuItem>
              <MenuItem onClick={logout}>
                <ListItemIcon>
                  <Logout fontSize="small" />
                </ListItemIcon>
                Logout
              </MenuItem>
            </Menu>
          </div>
        ) : (
          <div className="header-control">
            <div className="search">
              <InputBase
                id="search-input"
                sx={{ ml: 1, flex: 1 }}
                placeholder="搜索..."
                inputProps={{ "aria-label": "search google maps" }}
              />
              <IconButton type="button" sx={{ p: "10px" }} aria-label="search">
                <SearchIcon />
              </IconButton>
            </div>

            <NavLink to="/login" replace>
              登陆
            </NavLink>
          </div>
        )}
      </header>

      <SearchDialog ref={(el) => setDlog(el)} />

      <main style={{ height: "calc(100% - 70px)" }}>{renderConvert()}</main>
    </div>
  );
}

export default Home;
